*{
  margin-bottom: 30px;
  padding: 0;
  box-sizing: border-box;
}

body{
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to right,rgb(160, 155, 159),rgb(178, 61, 168));
  min-height: 100vh;
  width: 100%;
}

.container{
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 400px;
  width: 400px;
  background-color: white;
  border-radius: 15px;
  box-shadow: 2px 2px 10px 1px#2a2f92;
  overflow:hidden;
  padding: 10px;
  position: relative;
}
.heading-text{
  color: white;
  font-size: 30px;
  font-weight: bold;
  margin: 10px;
  text-align: center;
  text-transform: uppercase;
}
.question{
  margin: 8px;
  box-shadow: 0px 0px 10px 1px #a2a3a3;
  border-radius: 5px;
  padding: 5px;
  font-size: large;
  font-weight: 600;
  font-family: 'Times New Roman', Times, serif;
  min-width: 100%;
  min-height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#question-number{
  margin-right: 5px;
}
.option-container{
  display: flex;
  flex-direction: column;
  margin: 6px 4px;
  width: 100%;
  padding: 10px;
}

.option-btn{
  box-shadow: 0px 0px 4px 1px rgb(128,123,123);
  padding: 5px;
  border-radius: 3px;
  border: none;
  outline: none;
  transition: 0.3s;
  margin: 5px;
  min-height: 30px;
  background-color: white;
}
.checked{
  background-color: rgb(52,216,65);
  color: white;
  box-shadow: 3px 3px 4px 1px rgb(78,69,78);
}
.option-btn:hover{
  background-color: rgb(52,216,65);
  color: white;
}
#button{
  padding: 10px;
  border: none;
  background-color: purple;
  background-color: rgb(9, 88, 130);
  color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 2px 1px rgb(127,109,109);
  font-size: larger;
  font-weight: bold;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  transition: 0.2s;
  width: 100px;
  position: absolute;
  text-decoration: none;
  
}
#next-button{
  padding: 10px;
  border: none;
  background-color: purple;
  background-color: rgb(9, 88, 130);
  color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 2px 1px rgb(127,109,109);
  font-size: larger;
  font-weight: bold;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  transition: 0.2s;
  width: 100px;
  position: absolute;
  bottom: 15px;
  text-decoration: none;

}
#next-button:active{
  box-shadow: none;
}

.show-score{
  position: absolute;
  top: 30%;
  font-size: 40px;
  text-align: center;
}
